@font-face {
	font-family: Codystar;
	src: url('./fonts/Codystar-Regular.ttf');
}

body {
  background-color: #030910;
}

* {
  //font-family: 'Inter', system-ui !important;
  font-family: 'Codystar';
  font-weight: bold;
  box-sizing: border-box;
  user-select: none;
}

.App {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 30px;
  width: 900px;
  margin: 350px auto;
  background-color: rgb(0, 0, 0);
  padding: 30px;
  box-shadow: 0 0px 30px 5px rgb(185, 19, 19);
  border-radius: 4px;
  position: relative;
  
}

.block {
  input {
    border: 3px solid rgba(8, 139, 36, 0.1);
    background-color: black;
    color: rgb(189, 18, 18);
    font-size: 3rem;
    font-weight: bold;
    padding: 25px 25px;
    letter-spacing: 1.3px;
    outline: none;
    margin-top: 20px;
    border-radius: 5px;
    width: 100%;
    
  }
}

.currencies {
  display: flex;
  list-style: none;
  color: white;
  padding: 0;
  margin: 0;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  overflow: hidden;

  li {
    display: inline-flex;
    padding: 10px 20px;
    font-size: 20px;
    border-right: 1px solid rgba(0, 0, 0, 0.05);
    
    cursor: pointer;
    justify-content: center;
    flex: 1;

    &:hover {
      background-color: rgba(84, 204, 114, 0.03);
    }

    &:active {
      background-color: rgba(42, 6, 88, 0.05);
      color: white;
    }

    &.active {
      background-color: #067527;
      color: white;
    }
  }
}

h1 {
  top: -160px;
  font-size: 36px;
  color: white;
  border: 2px dotted rgb(23, 107, 6);
  padding: 20px;
  box-shadow: #067527 0px 1px 24px 6px;

  left: 25%;
  position: absolute;
  font-family: 'Codystar';
  font-weight: bold;
}
h5 {
  
  font-size: 26px;
  color: white;
}

span {
  color: #0c7550;
}

p {
  color:  white;
}

h3 {
  color: white;
  width: 100%;
}

.buySell {
  font-size: 34px;
  color: green;
}

.buySellCurrency {
font-size: 32px;
color: red;
}

.choosePage {
  position: fixed;
  left: 0;
  top: 0;
  text-decoration: none;
  color: white;
  font-size: 20px;
}
.choosePage:hover {
  color: rgb(192, 240, 183);
  
}